import { Node } from '@tiptap/core';

/**
 * SpoilerInlineParagraph -  >!text!< 
 * 
 *  blockquote  >!text!< 
 *  SpoilerBlock  ">! text"
 * 
 * 
 * - ">!spoiler!<"   spoilerInline mark
 * - ">! text"   SpoilerBlock 
 */
export const SpoilerInlineParagraph = Node.create({
    name: 'spoilerInlineParagraph',

    //  schema
    //  markdown  paragraph + spoilerInline mark

    // Markdown token 
    markdownTokenName: 'spoiler_inline_paragraph',

    //  Markdown tokenizer:  >!text!< 
    markdownTokenizer: {
        name: 'spoiler_inline_paragraph',
        level: 'block',
        start: (src: string) => {
            //  >!  >! text 
            const match = /^>![^\s]/.exec(src);
            return match ? 0 : -1;
        },
        tokenize: (src: string, tokens: any[]) => {
            // : >!content!< ( spoiler )
            //  >! 
            const lineMatch = /^(.*?)(?:\n|$)/.exec(src);
            if (!lineMatch) return undefined;
            
            const line = lineMatch[0];
            
            //  >!text!<  >! text ()
            if (/^>! /.test(line)) {
                return undefined; //  SpoilerBlock 
            }
            
            //  >!...!< 
            if (!/^>![^!]+!</.test(line)) {
                return undefined;
            }
            
            return {
                type: 'spoiler_inline_paragraph',
                raw: line,
                text: line.replace(/\n$/, ''),
            };
        },
    },

    // Token  Tiptap JSON
    //  >!text!<  spoilerInline mark
    parseMarkdown: (token: any, helpers: any) => {
        const text = token.text;
        const content: any[] = [];
        
        //  >!text!<  ||text|| 
        const spoilerRegex = />!([^!]+)!</g;
        const pipeRegex = /\|\|([^|]+)\|\|/g;
        
        let lastIndex = 0;
        let combinedMatches: { index: number; length: number; text: string; type: 'spoiler' }[] = [];
        
        //  >!text!< 
        let match;
        while ((match = spoilerRegex.exec(text)) !== null) {
            combinedMatches.push({
                index: match.index,
                length: match[0].length,
                text: match[1],
                type: 'spoiler',
            });
        }
        
        //  ||text|| 
        while ((match = pipeRegex.exec(text)) !== null) {
            combinedMatches.push({
                index: match.index,
                length: match[0].length,
                text: match[1],
                type: 'spoiler',
            });
        }
        
        // 
        combinedMatches.sort((a, b) => a.index - b.index);
        
        // 
        for (const m of combinedMatches) {
            //  spoiler 
            if (m.index > lastIndex) {
                const plainText = text.slice(lastIndex, m.index);
                if (plainText) {
                    content.push({ type: 'text', text: plainText });
                }
            }
            
            //  spoiler  mark
            content.push({
                type: 'text',
                text: m.text,
                marks: [{ type: 'spoilerInline' }],
            });
            
            lastIndex = m.index + m.length;
        }
        
        // 
        if (lastIndex < text.length) {
            const remaining = text.slice(lastIndex);
            if (remaining) {
                content.push({ type: 'text', text: remaining });
            }
        }
        
        return {
            type: 'paragraph',
            content: content.length > 0 ? content : undefined,
        };
    },

    //  renderMarkdown paragraph + spoilerInline mark 
});

export default SpoilerInlineParagraph;
